import {
	basePath
} from '@/utils/index'
import {
	mapState,
	mapMutations
} from 'vuex'
import api from "@/api/category"
import memberApi from "@/api/member"
import tuiIcon from "@/components/icon/icon"
import tuiTag from "@/components/tag/tag"
import tuiLoadmore from "@/components/loadmore/loadmore"
import tuiNomore from "@/components/nomore/nomore"
export default {
	components: {
		tuiIcon,
		tuiTag,
		tuiLoadmore,
		tuiNomore
	},
	data() {
		return {
			basePath,
			current: 0,
			tabbar: [{
				icon: "home",
				text: "首页",
				size: 21
			}, {
				icon: "category",
				text: "分类",
				size: 24
			}, {
				icon: "cart",
				text: "购物车",
				size: 22
			}, {
				icon: "people",
				text: "我的",
				size: 24
			}],
			banner: [
				"1.jpg",
				"2.jpg",
				"3.jpg",
				"4.jpg",
				"5.jpg"
			],
			category: [],
			productList: [],
			pageIndex: this.$pagination.page,
			lastPage: this.$pagination.page,
			searchKey: '', //关键字
			loadding: false,
			pullUpOn: true
		}
	},
	onLoad() {
		/* 判断是否登录过 */
		if (this.hasLogin && this.member_id) {
			this.getHostProduct();
		} else {
			/* 重新登录 */
			let $me = this;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					let code = loginRes.code;
					$me.islogin(code)
				},
			})
		}
		this.getBannerImg();
		this.getCategory();

	},
	watch: {
		pageIndex(newValue, oldValue) {
			if (this.pageIndex == this.lastPage) {
				console.log(22222)
				this.pullUpOn = false;

			}
		}
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'member_id', 'openid']),
	methods: {
		...mapMutations(['login', 'setCate']),
		//获取轮播图
		getBannerImg() {
			this.$postajax(memberApi.getBannerImg)
				.then(res => {
					if (res.code == 0) {
						this.banner = res.data;
					}
				})
				.catch(err => {

				});
		},
		//获取分类
		getCategory() {
			var param = {
				page: this.pageIndex,
				limit: this.$pagination.limit,
			}
			this.$postajax(api.getCate, param)
				.then(res => {
					if (res.code == 0) {
						this.category = res.data;
					}
				})
				.catch(err => {

				});
		},
		//获取产品
		getHostProduct() {
			var param = {
				page: this.$pagination.page,
				limit: this.$pagination.limit,
				member_id: this.member_id,
				name: this.searchKey,
			}
			this.$postajax(api.getHostProduct, param)
				.then(res => {
					if (res.code == 0) {
						this.productList = res.data;
						this.lastPage = res.data && res.data.length > 0 ? (Math.ceil(res.count / this.$pagination.limit)) : 1;
						console.log("最后一页" + JSON.stringify(this.lastPage))
						if (this.pageIndex == this.lastPage) {
							this.pullUpOn = false;
						}
						//this.count=res.count;
					}


				})
				.catch(err => {

				});

		},
		tabbarSwitch: function(e) {
			let index = e.currentTarget.dataset.index;
			this.current = index;
			if (index != 0) {
				if (index == 1) {
					this.classify();
				} else if (index == 2) {
					uni.navigateTo({
						url: '../mall-extend/shopcart/shopcart'
					})
				} else {
					uni.navigateTo({
						url: '../mall-extend/my/my'
					})
				}
			}
		},
		detail: function(item) {
			console.log(JSON.stringify(item))
			/* ar productBaseInfo={
					"name":item.name,
				    "price":item.price,
				    "id":item.id,
				    "img_url":item.img_url
			} */
			uni.navigateTo({
				url: '../extend-view/productDetail/productDetail?id=' + item.id
			})
		},
		//banner跳转页面，
		bannerSkip(url) {
			console.log(url)
			if (url.indexOf('category') > -1) {
				uni.switchTab({
					url: url
				})
			} else {
				uni.navigateTo({
					url: url
				})
			}
		},
		classify: function(id) {
			if (id) {
				this.setCate(id)
			}
			uni.switchTab({
				url: '../category/category'
			});

		},
		more: function(e) {
			let key = e.currentTarget.dataset.key || "";
			uni.navigateTo({
				url: '../extend-view/productList/productList?searchKey=' + key
			})

		},
		//模糊搜索
		search: function() {
			this.getHostProduct();
		},
		//清空搜索关键字
		cleanKey: function() {
			this.searchKey = '';
			this.getHostProduct();
		},

		islogin(code) {
			//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
			var param = {
				code: code
			}
			var $me = this;
			$me.$postajax(memberApi.login, param)
				.then(res => {
					console.log("登录返回" + JSON.stringify(res))
					if (res.code == 0) {
						$me.login(res.data);
						this.getHostProduct();
					} else {}
				})
				.catch(err => {

				});
		}
	},
	onReachBottom: function() {
		console.log(3333)
		//if (!this.pullUpOn) return;
		this.pullUpOn = true
		this.loadding = true;

		console.log('页面上拉触底事件');
		// 每次拉到页面底部,请求页数自加
		this.pageIndex++;
		// 第二次之后请求数据
		if (this.pageIndex > this.lastPage) {
			setTimeout(() => {
				this.loadding = false
				this.pullUpOn = false
			}, 1000)
			return false;
		} else {
			var param = {
				page: this.pageIndex,
				limit: this.$pagination.limit,
				member_id: this.member_id,
				name: this.searchKey,
			}
			this.$postajax(api.getHostProduct, param)
				.then(res => {
					console.log(JSON.stringify(res))
					if (res.code == 0) {
						console.log(JSON.stringify(res))
						if (res.code == 0) {
							this.productList = this.productList.concat(res.data)
							this.loadding = false;
						}
					}


				})
				.catch(err => {

				});
		}
	}
}
